import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React, { useEffect } from 'react';
import Cards from './Cards';
import DropZoneGuide from './components/DropZoneGuide';
import RightSideBar from './components/RightSideBar';
import { useGlobalStore, useStore } from './hooks/useStore';

const App = () => {
  const { isDragging, setIsDragging, confetti, confettiRandom } =
    useGlobalStore();
  const { loadData } = useStore();

  const onDragEnterHandler = (e: React.DragEvent<HTMLDivElement>): void => {
    e.preventDefault();
    e.stopPropagation();

    if (e.dataTransfer.types[0] !== 'Files') return;

    setIsDragging(true);
  };

  const onDragLeaveHandler = (e: React.DragEvent<HTMLDivElement>): void => {
    e.preventDefault();
    e.stopPropagation();
    const { relatedTarget } = e;

    if (!relatedTarget) {
      setIsDragging(false);
      return;
    }

    if (!(relatedTarget instanceof HTMLElement)) return;
    if (!(relatedTarget.tagName === 'HTML')) return;
    setIsDragging(false);
  };

  const onDragOverHandler = (e: React.DragEvent<HTMLDivElement>): void => {
    e.preventDefault();
    e.stopPropagation();
  };

  const onDropHandler = async (
    e: React.DragEvent<HTMLDivElement>,
  ): Promise<void> => {
    e.preventDefault();
    e.stopPropagation();
    if (!isDragging) return;

    setIsDragging(false);

    const { files } = e.dataTransfer;

    if (files.length !== 1) {
      alert('.json 파일 한개만 필요합니다');
      return;
    }

    const data = files[0];

    const jsonString = await data.text();

    loadData(jsonString);
  };

  useEffect(() => {
    const handler = (e: KeyboardEvent): void => {
      if (e.key === '1') {
        confetti();
      }
      if (e.key === '2') {
        confettiRandom();
      }
    };
    window.addEventListener('keydown', handler);

    return () => {
      window.removeEventListener('keydown', handler);
    };
  }, [confetti, confettiRandom]);

  return (
    <>
      <div css={styleWrapper}>
        <div
          css={styleMain()}
          onDragEnter={onDragEnterHandler}
          onDragLeave={onDragLeaveHandler}
          onDragOver={onDragOverHandler}
          onDrop={onDropHandler}
        >
          <Cards />

          <RightSideBar />
        </div>
      </div>

      {isDragging && <DropZoneGuide />}
    </>
  );
};

export default observer(App);

const styleWrapper = css`
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: #e5eeff;
`;

const styleMain = () => css`
  font-size: 3rem;
  display: grid;
  grid-template-columns: auto 400px;
  gap: 60px;
  justify-content: center;
  align-items: center;
`;
